.creators-section {
  padding: 4.8rem 0;
}

.creators-heading-box {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8rem;
  display: flex;
}

.creators-grid {
  height: 40rem;
}

.creator-card {
  background: linear-gradient(113.49deg, #c73d54 -30.3%, #16256b 58.12%);
  border-radius: 11px;
  flex-direction: column;
  align-items: center;
  transition: all .4s;
  display: flex;
  position: relative;
  overflow: hidden;
  box-shadow: 0 2.4rem 4.8rem #00000013;
}

.creator-card:hover {
  transform: translateY(-1.2rem);
  box-shadow: 0 0 6.4rem #daa520;
}

.creator-banner {
  object-fit: cover;
  width: 100%;
  height: 40%;
}

.creator-img {
  border: 2px solid #e3590f;
  border-radius: 10px;
  width: 25%;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 12px 24px #0003;
}

.creator-info {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  margin-top: 2.4rem;
  padding: 2.4rem;
  display: grid;
}

.creator-name {
  color: #ded6d6;
  grid-column: 1 / -1;
  font-size: 2.4rem;
  font-weight: 600;
}

.creator-stat {
  color: #ded6d6;
  flex-direction: column;
  align-items: center;
  gap: 1.2rem;
  font-size: 1.6rem;
  display: flex;
}

.creator-stat p:nth-child(2) {
  color: #daa520;
  font-size: 2rem;
  font-weight: 500;
}

.creator-button {
  background-color: #ff4500;
  width: 75%;
  padding: .8rem 1.2rem;
  font-size: 1.6rem;
}

.creator-button:hover, .creator-button:active {
  background-color: #dc4b05;
}
